import originAxios from 'axios';
import axios from './axios';

/**
 * 
 * @param {String} email 
 * @returns {Promise}
 */
export const checkEmailExistent = (email, cancelToken = {}) => {
  const { CancelToken } = originAxios;
  const source = CancelToken.source();
  const req = axios({
    method: 'get',
    url: '/authcenter/user/email/check',
    params: { email },
    cancelToken: source.token,
  });
  cancelToken.cancel = source.cancel;
  return req;
};

