/**
 * 
 * @param {String} email
 * @returns {Promise}
 */
export const sendVerifyCodeByRegistration = (email, lang) => {
  return axios({
    method: 'post',
    // url: '/authcenter/user/email/verify/code',
    // data: { email },
    url: '/authcenter/code/send',
    data: {
      email,
      type: 1,
    },
    headers: {
      'sdstc-language': lang,
    },
  });
};

